  },
  watch: {},
  created () {},
  mounted () {},
  methods: {
    tabClick(tab) {
      // console.log(tab.index);
      let tabindex = Number(tab.index)
      this.rightTabline1 = 'standard1'
      this.rightTabline2 = 'standard2'
      this.patterntitle = '标准',
      this.patternline1 = '• 类似于 Uniswap',
      this.patternline2 = '• 可以设置更多参数'
      if(tabindex == 0){
        this.typetitle ='公共池',
        this.typeline1 ='• 任何人可以添加流动性',
        this.typeline2 ='• 资金池一旦创建，参数无法修改'
      }else if(tabindex == 1){
        this.typetitle ='私有池',
        this.typeline1 ='• 只有你才能添加流动性',
        this.typeline2 ='• 资金池创建后参数可随时灵活调整'
      }else{
        this.typetitle ='锚定资产池',
        this.typeline1 ='• 任何人可以添加流动性，资金池一旦创建，参数无法修改',
        this.typeline2 ='• 曲线类似于 Curve，适用于合成资产'
      }
    },
    tabClickLine1(tab){
      let tabindex = Number(tab.index)
      if(tabindex == 0){
        this.patterntitle = '标准',
        this.patternline1 = '• 类似于 Uniswap',
        this.patternline2 = '• 可以设置更多参数'
      }else if(tabindex == 1){
        this.patterntitle = '单边',
        this.patternline1 = '• 出售代币',
        this.patternline2 = '• 支持单币建池'
      }else{
        this.patterntitle = '自定义',
        this.patternline1 = '• 你可以配置交易手续费率、 滑点系数和最低价格',
        this.patternline2 = ''
      }
    }
  }
}
</script>
